import React, {Component}  from 'react';
import { LineChart, Line, XAxis, YAxis } from 'recharts';
//import logo from './logo.svg';
import Tone from 'tone';
import './App.css';

class App extends Component{
  //state = {};
  constructor() {
    super();
    this.state = {
      fft: new Tone.Analyser(),
      waveform: new Tone.Analyser('waveform', 32),
      mic: new Tone.UserMedia(),
      recordTxt: "Record",
      temp: [],
      max: 0
    };

  }

  record = () => {
    let {mic, recordTxt, waveform, fft} = this.state;
    if (recordTxt === "Record") {
      mic.open().then(() => {
        console.log('mic open!');
        mic.connect(fft).connect(waveform);
        this.setupPitch();
      });
      this.setState({recordTxt: "Stop"});
    } else {
      console.log('mic close!');
      mic.close();
      this.setState({recordTxt: "Record"});
    }
    
    
  }

  setupPitch = () => {
    let {waveform, fft} = this.state;

   // mic.toMaster();
    //PITCH
    //var pitch1 = new Tone.PitchShift().toMaster();
    //var pitch2 = new Tone.PitchShift().toMaster();
    //Connect Mic to Pitches
    //mic.connect(pitch1).connect(pitch2);
    //Pitch1 and Pitch together with Mic create a Major Third Chord
    //pitch1.pitch = 4; //one third up
    //pitch2.pitch = -4; //one third down

    setInterval(() => {
      var waveData = waveform.getValue();
      var max = Math.max.apply(Math, waveData);
      var min = Math.min.apply(Math, waveData)*-1;
      var r = Math.max(max, min);
      r *= 7;
      if (r > 1) {
          r = 1.;
      } else if (r < 0.2 && r !== 0) {
          r = 0.2;
      }

      var fftValue = fft.getValue();
      
      //fftValue = fftValue.subarray(0, N/2 - 1);
      max = Math.max(...fftValue);
      //min = Math.min(...fftValue);
      //var tempMax = max > -min ? max : min;
      var fs = fft.context.sampleRate;
      var N = fft._analyser.fftSize;
      var fftMax = fftValue.indexOf(max) * fs / N;
      
      //console.log(fft.getValue());
      //console.log(fft);
      var data = [];
      for (let i=0; i<fftValue.length; i++) {
        data.push({name: Math.floor(i * fs / N), intensity: fftValue[i]});
      }

      console.log(fftMax, r);
      this.setState((prevState) => ({
        temp: data, 
        max: r === 0.2 ? prevState.max : Math.floor(fftMax)
      }));


      //console.log(mic.context);
     //console.log(Tone.Master.volume.value);
    }, 200)
  }

  render() {

    //return <MyChart />;
    return (
    <div>
      <button onClick={this.record}>{this.state.recordTxt}</button>
      <MyChart data={this.state.temp}></MyChart>
      <span>{this.state.max}</span>
    </div>
    );
  }

  

}


